import React, { Component } from 'react';

class ThankYou extends Component {
  static async getInitialProps({query}) {
    return {slug: query.params}
  }
  render() {
    return (
      <h1>Thank you! {this.props.slug}</h1>
    );
  }
}

export default ThankYou;



